

var obj = {
	methods:{
		// checkLogins  
		checkLogins(val){
			if(this.checkHasLogined()){
				val();
			}
		},
		checkHasLogined(){
			var token = uni.getStorageSync("leju_token");
			if(token){
				return true
			}else{
				uni.showModal({
					title:"",
					content:"",
					success(res){
						if(res.confirm){
							
							uni.navigateTo({
								url:"/pages/user/login/login"
							})
						}else{
							uni.showToast({
								title:""
							})
						}
					}
				})
				return false
			}
		}
	}
}
export default obj