var obj = {
	//  

	onShareAppMessage(res) {
		 var pages = getCurrentPages();
		 var path = pages[pages.length - 1].route;
		 var params =  pages[pages.length - 1].options;
		 if(params){
			 for(var i in params){
				 path = path+`?${i}=${params[i]}`
			 }
		 }
	   if (res.from === 'button') {// 
	     console.log(res.target)
	   }
	   return {
	     title: '',
	     path: path
	   }
	}

}

export default obj